.notfound_container{
    height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.notfound_container h2{
    color: red;
}
.notfound_container p{
    margin: 2rem 0;
    line-height: 1.5;
}

@media (max-width:1024px){
    .notfound_container{
        height: 80vh;
    }
}
@media (max-width:600px){
    .notfound_container{
        height: 75vh;
    }
    .notfound_container p{
        margin: 1.5rem 0.5rem;
        line-height: 1.5;
    }
}