@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&family=Nunito:ital,wght@0,200;0,500;0,600;1,300&family=Poppins:wght@300;400;500;600;700;800;900&family=Roboto:wght@300;400;500;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
}

:root {
  --white-color: #fff;
  --black-color: #000;
  --blue-color: rgb(98 84 243);
  --hr-color: #ffffff;
  --bg-color: rgb(249, 249, 255);
  --footer_bg: #0a1435;
  --yellow-color: rgba(244, 178, 22, 0.922);

  --transition: all 500ms ease;

  --container-width-lg: 75%;
}

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

section {
  margin-top: 8rem;
}
img{
  width: 100%;
  display: inline-block;
  object-fit: cover;
}
.btn {
  display: inline-block;
  color: var(--white-color);
  background: var(--blue-color);
  padding: 0.8rem 1.5rem;
  font-size: 1.1rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.btn:hover {
  background: transparent;
  color: var(--blue-color);
  border-color: var(--blue-color);
}

a {
  color: var(--white-color);
  font-size: 1.1rem;
}

a:hover {
  color: var(--yellow-color);
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.3rem;
  text-align: center;
}

/* ==================Logo===================== */
.logo{
  border: 2px solid var(--white-color);
  font-size: 1.4rem;
  font-weight: 600;
  border-radius: 5px;
}
.logo span{
  color: var(--blue-color);
  background: var(--white-color);
}

/* ==================Social_contact===================== */
.contiainer_social{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 1rem;
}
.social{
  margin-top: 1rem;
}
.social a {
  margin: 0 0.6rem;
  color: green;
  background: var(--white-color);
  font-size: 1.6rem;
  padding: 0.5rem 0.5rem 0;
  border-radius: 50%;
  border: 1px solid transparent;
  transition: var(--transition);
  --webkit-box-shadow: 0 4px 22px 0 rgba(0, 0, 0, 0.75);
  --moz-box-shadow: 0 4px 22px 0 rgba(0, 0, 0, 0.75);
  box-shadow: 0 4px 22px 0 rgba(119, 245, 56, 0.971);
}

.social a:hover {
  background: transparent;
  color: black;
  border: 1px solid var(--white-color);
}