.contact{
    height: 80vh;
    text-align: center;
}
.contact h2{
    text-align: center;
    margin: 10rem 0 3rem;
    color: var(--blue-color);
}
.contact_container > div{
    height: 60vh;
    display: grid;
    grid-template-columns: repeat(2, auto);
    gap: 2rem;
    border-radius: 1rem;
    background: rgba(170, 157, 231, 0.799);
}
.contact_content, .contact_input{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contact_content h3{
    color: var(--white-color);
    font-size: 2rem;
    margin-bottom: 2rem;
}
.contact_input input, textarea{
    width: 80%;
    padding: 1rem 0.5rem;
    border: 1px solid grey;
    margin: 0.5rem;
}

.btn1{
    margin-top: 1rem;
}

@media (max-width:1024px){
    .contact_container > div{
        grid-template-columns:repeat(2, auto);
        gap: 0rem;
    }  
}
@media (max-width:600px){
    .contact{
        height: 100vh;
    }
    .contact h2{
        margin: 0 0 1rem;
    }
    .container{
        width: 100%;
    }
    .contact_container > div{
        width: 95%;
        margin: 0 auto;
        height: 100vh;
        grid-template-columns: auto;
    }  
    .contact_input input, textarea{
        width: 90%;
        padding: 0.8rem 0.4rem;
        margin: 0.3rem;
    }
}

