nav {
    width: 100%;
    background: var(--blue-color);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}

.navbar {
    margin: 0 15%;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav_links {
    display: flex;
    align-items: center;
    gap: 5rem;
}

.linkbtn {
    display: none;
}

