/* ==============Mobile-device=================== */
@media (max-width:768px) {
    .linkbtn {
        display: block;
        font-size: 1.5rem;
    }

    .nav_links {
        display: flex;
        flex-direction: column;
        background: var(--blue-color);
        position: absolute;
        right: 4rem;
        top: 4rem;
        align-items: center;
        justify-content: center;
        gap: 0rem;
    }

    .nav_links li {
        width: 11rem;
        height: 5rem;
        box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.3);
    }

    .nav_links li a {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
    }

    .active::after {
        display: none;
    }

    .show_nav {
        display: flex;
    }

    .hide_nav {
        display: none;
    }
}
@media (max-width:1024px) {
    .nav_links {
        gap: 2.5rem;
    }
}