.home_container > div{
    height: 80vh;
    display: grid;
    align-items: center;
    justify-content: center;
    grid-template-columns: repeat(2, auto);
    gap: 1rem;   
}
.home_img, .home_content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 15rem;
}
.home_content h1{
    margin: 0.6rem 0;
}
.home_content p{
    margin-bottom: 2rem;
}

@media (max-width:1024px){
    .home_container > div{
        height: 90vh;
        gap: 1rem;   
    }
    .home_img, .home_content{
        margin-left: 8rem;
    }
    .home_content h1{
        margin: 0.6rem 0;
    }
}
@media (max-width:600px){
    .container{
        width: 100%;
    }
    .home_container > div{
        height: 80vh;
        grid-template-columns: auto;
    }
    .home_img, .home_content{
        margin-left: 0rem;
    }
}