.abouts h2{
    text-align: center;
    margin: 10rem 0 0;
    color: var(--blue-color);
}
.about_container{
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.about_container >div{
    display: grid;
    grid-template-columns: repeat(3, auto);
    gap: 3rem;
    line-height: 1.8;
}
.about > div{
    color: var(--white-color);
    border: 1px solid var(--blue-color);
    padding: 2rem;
    border-radius: 2rem;
    background: var(--blue-color);
    transition: var(--transition);
}
.about >div:hover{
    color: var(--blue-color);
    background: transparent;
}
.education p span{
    font-size: 1.1rem;
    font-weight: bolder;
    color: rgb(245, 140, 42);
}
.education h3, .achievement h3, .certificate h3{
    color: var(--yellow-color);
}

@media (max-width:1024px){
    .container{
        width: 95%;
    }
    .about_container{
        height: 90vh;
    }
    .about_container >div{
        align-items: center;
        grid-template-columns: repeat(2, auto);
        gap: 0.8rem;
    }
}
@media (max-width:600px){
    .abouts h2{
        margin: 10rem 0 6rem;
    }
    .about_container{
        height: 100vh;
    }
    .about_container >div{
        width: 97%;
        grid-template-columns: auto;
        gap: 1.5rem;
    }
}