.skills h2{
    text-align: center;
    margin: 10rem 0 7rem;
    color: var(--blue-color);
}
.skill_container{
    height: 60vh;
}
.skill_container > div{
    display: grid;
    grid-template-columns: repeat(2, auto);
    gap: 2rem;
}
.skill > div{
    width: 600px;
    height: 50vh;
    background: #fff;
    padding: 5px 0;
    border-radius: 1rem;
    --webkit-box-shadow:0 4px 22px 0 rgba(0, 0, 0, 0.75);
    --moz-box-shadow:0 4px 22px 0 rgba(0, 0, 0, 0.75);
    box-shadow:0 4px 22px 0 rgba(72, 94, 236, 0.75);
    
}
.skill_content h3, .projects h3{
    margin: 1.5rem 0 1.8rem;
}
.skill_content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.scope{
    color: #fff;
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 8px 0;
    background-image:linear-gradient(rgb(80, 80,80), rgb(22, 22,22)) ;
    padding: 10px 5%;
    text-align: center;
    border-radius: 5px;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.85);
}
progress{
    border-radius: 15%;
    width: 60%;
    height: 16px;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.89);
    transform: scaleX(0);
    transform-origin: left;
    animation: grow 1s ease-out forwards;
}
progress::-webkit-progress-bar{
    background-image: linear-gradient(rgb(114, 114, 114), rgb(29, 29, 29));
    border-radius: 10px;
}
progress::-webkit-progress-value{
    background-image: linear-gradient(to right, rgb(33, 114, 26),rgb(0, 255, 0));
    border-radius: 7px;
    box-shadow: 2px 0px 2px 1px rgb(55, 245, 7);
}
.scope:hover{
    transition: var(--transition);
    cursor: pointer;
    background-image: linear-gradient(rgb(24, 16, 111), rgb(49, 18, 67));
    transform: scale(1.02);
}

@keyframes grow{
    100%{
        transform:scaleX(1)
    }
}




.projects{
    line-height: 1.5;
    padding: 1rem;
}
.projects p{
    margin: 1rem 2rem;
}
.projects p span{
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--blue-color);
}


@media (max-width:1024px){
    .skill_container{
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .skill_container > div{
        grid-template-columns:auto;
    }
    .projects{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
    }
 .projects h3{
        margin:0 auto;
        text-align: center;
    }

}
@media (max-width:600px){
    .skill > div {
        width: 390px;
        height: 50vh;
        background: #fff;
        padding: 5px 0;
        border-radius: 1rem;
    }
}